@use '../../Variables.scss' as var;
@import '../../assets/styles/Utils.scss';
@import '../../assets/styles/Adaptation.scss';

.Home {
  @include flex;
  
  width: 100vw;
  height: 100vh;
  color: #fff;
  background: #000;
}

.HomeLogo {
  position: relative;
  z-index: 9;
  width: pw(471);
  text-align: center;
}

.HomeLogoText {
  font-size: pw(24);
  margin-top: 10px;
}

.HomeLogo img {
  width: 100%;
}

.Background {
  position: absolute;
  left: 0;
  top: 0;
  width: 100vw;
  height: 100vh;
  padding: 60px;
  overflow: hidden;
}

.BackgroundVideo {
  object-fit: contain;
  box-sizing: border-box;
  width: 100%;
  height: 100%;
  // mix-blend-mode: overlay;
}

@media (max-width: 768px) {
  .HomeLogo {
    position: relative;
    z-index: 9;
    width: 200px;
    text-align: center;
  }

  .BackgroundVideo {
    object-fit: contain;
    width: 200%;
    height: 100%;
    margin-left: -50%;
    // margin-top: -60%;
  }

  .HomeLogoText {
    font-size: 12px;
    margin-top: 10px;
  }
}

.Arrow,
.Arrow:before {
  position: absolute;
  left: 50%;
}

.Arrow {
  width: 40px;
  height: 40px;
  top: 90%;
  margin: -20px 0 0 -20px;
  transform: rotate(45deg);
  border-left: none;
  border-top: none;
  border-right: 2px #fff solid;
  border-bottom: 2px #fff solid;
}

.Arrow:before {
  content: '';
  width: 20px;
  height: 20px;
  top: 50%;
  margin: -10px 0 0 -10px;
  border-left: none;
  border-top: none;
  border-right: 1px #fff solid;
  border-bottom: 1px #fff solid;
  animation-duration: 2s;
  animation-iteration-count: infinite;
  animation-name: arrow;
  transform-origin: center;
}

@keyframes arrow {
  0% {
    opacity: 1;
    transform: translate(-15px, -15px);
  }
  100% {
    opacity: 0;
    transform: translate(-10px, -10px);
  }
}
