@use '../../Variables.scss' as var;
@import '../../assets/styles/Utils.scss';
@import '../../assets/styles/Adaptation.scss';

.Partners {
  @include flex;
  flex-direction: column;
  padding: 80px pw(360) pw(30);
  overflow: auto;
  width: 100%;
  height: 100%;
}

.PartnersContainer {
  width: 100%;
}

.PartnersImage {
  width: 100%;
  height: pw(80);
}

.PartnersTitle {
  font-weight: 900;
  font-size: pw(48);
  color: #ffffff;
}

.PartnersContent {
  font-weight: 400;
  font-size: pw(18);
  color: var.$color-text;
  opacity: 0.7;
}

.TextCenter {
  text-align: center;
  margin: pw(20) 0 pw(30);
}

.PartnersSecTitle {
  font-weight: 900;
  font-size: pw(24);
  color: #ffffff;
}

.Conversation {
  margin-top: pw(220);
}

.Card {
  @include flex(space-between);
  margin-top: pw(60);
  flex-wrap: wrap;
  align-items: stretch;
}

.CardItem {
  width: 32%;
  // width: pw(360);
  // height: pw(260);

  // background: #0a2809;
  background: #212121;
  border-radius: 20px;
  padding: pw(30);
  cursor: pointer;
  margin-bottom: pw(20);
  color: #fff;

  &:hover {
    background: rgba($color: #212121, $alpha: 0.9);
    // border: 1px solid rgba(255, 255, 255, 0.2);
  }
}

.CardItemLogo {
  width: pw(150);
  height: pw(150);
  margin-bottom: pw(0);
}

.CardItemTitle {
  font-style: normal;
  font-weight: 900;
  font-size: pw(16);
  margin: pw(10) 0;
}

.CardItemContent {
  font-style: normal;
  font-weight: 400;
  font-size: pw(15);
  opacity: 0.7;
}

.ConversationCard {
  @include flex(space-between);

  flex-wrap: wrap;
  margin-top: pw(40);
}

.ConversationCardItem {
  width: 49%;
  @include flex(flex-start);
  border: 1px solid rgba(255, 255, 255, 0.2);
  border-radius: pw(20);
  padding: pw(23) pw(10);
  cursor: pointer;
  margin-bottom: pw(20);

  &:hover {
    background: radial-gradient(
      139.76% 139.76% at 50% -39.76%,
      #020f1c 0%,
      rgba(63, 154, 85, 0.49) 100%
    );
    border: 1px solid rgba(255, 255, 255, 0.2);
  }
}

.ConversationCardLogo {
  width: pw(40);
  height: auto;
  margin-right: pw(30);
  margin-left: pw(20);
}

.ConversationCardTitle {
  font-style: normal;
  font-weight: 900;
  font-size: pw(20);
  color: #3f9a55;
}

.ConversationCardContent {
  font-style: normal;
  font-weight: 400;
  font-size: pw(15);
  color: var.$color-text;
}

.Mail {
  @include flex(flex-start);
  max-width: 477px;
  background: #212121;
  border: 1px solid rgba(255, 255, 255, 0.2);
  border-radius: 20px;
  padding: 20px;
  margin-top: 30px;
}

.Mail img {
  width: 57px;
  height: 45.5px;
  margin-right: 30px;
}

.MailTitle {
  font-weight: 800;
  font-size: 20px;
  color: #ffffff;
}

@media (max-width: 768px) {
  .CardItem {
    width: 100%;
    padding: pw(60);
    margin-bottom: pw(40);
  }

  .ConversationCardItem {
    width: 100%;
  }

  .Partners {
    padding: 0 15px pw(30) !important;
  }

  .CardItemLogo {
    width: 160px;
    height: 160px;
  }

  // .Partners {
  //   & * {
  //     font-size: 14px !important;
  //   }
  // }
}
