@use '../../Variables.scss' as var;
@import '../../assets/styles/Utils.scss';
@import '../../assets/styles/Adaptation.scss';

.RoadmapWarp {
  @include flex;
  flex-direction: column;
  padding: 90px 35px;
  overflow: auto;
}

.Roadmap {
  width: pw(650);
  position: relative;
  z-index: 9;
  padding-top: 60px;
}

.RoadmapTitle {
  width: 100%;
  font-weight: 900;
  font-size: pw(48);
  text-align: left;
  margin-bottom: pw(30);
  color: #fff;
}

.RoadmapItem {
  width: pw(360);
  height: auto;
  background: rgba(var.$color-card-background, 1);
  border: 2px solid var.$color-primary;
  border-radius: 20px;
  padding: pw(30);
  margin-bottom: pw(40);
  position: relative;
  z-index: 1;
  color: #fff;
}

.RoadmapItemIconContainer {
  @include flex(flex-end);
  margin-bottom: pw(34);
}

.RoadmapContainerStart {
  @include flex(flex-start);
}

.RoadmapContainerEnd {
  @include flex(flex-end);
}

.RoadmapItemTime {
  font-style: normal;
  font-weight: 900;
  font-size: pw(18);
}

.RoadmapItemContent {
  font-weight: 400;
  font-size: pw(16);
  color: #ffffff;
  opacity: 0.7;
}

.ListLine {
  width: 1px;
  height: pw(1350);
  position: absolute;
  left: pw(320);
  border-left: 1px dashed var.$color-primary;
}

@media (max-width: 768px) {
  .Roadmap {
    width: 100%;
    margin-left: 0;
    z-index: 99;
    padding: 0 15px;
  }
  .Roadmap2Video {
    display: none;
  }

  .RoadmapItem {
    width: 100%;
  }

  .ListLine {
    display: none;
  }
}
