@use 'sass:math';

@function pw($size) {
  @return math.div($size * 1px, 1920px) * 100vw;
}

@function ph($size) {
  @return math.div($size * 1px, 1080px) * 100vh;
}

@mixin flex($justify-content: center, $align-items: center, $align-content: center) {
  display: flex;
  justify-content: $justify-content;
  align-items: $align-items;
}
