@use '../../Variables.scss' as var;
@import '../../assets/styles/Utils.scss';
@import '../../assets/styles/Adaptation.scss';

.Volunteer {
  @include flex;
}

.VolunteerLeft {
  width: 50vw;
  height: 100vh;
  background: #00193e;
}

.VolunteerRight {
  width: 50vw;
  height: 100vh;
  background: #002d71;
  padding: pw(100) pw(180);
}

.Title {
  font-weight: 800;
  font-size: pw(48);
  color: #ffffff;
}

.Card {
  @include flex(flex-start, flex-start);

  // width: pw(418);
  // height: pw(386);
  border: 1px solid var.$color-primary;
  border-radius: 20px;
  position: relative;
  padding: 0 pw(42) pw(20);
  margin-top: 30px;
}

.CardLeft {
  @include flex(center);
  flex-direction: column;
}

.CardRight {
  margin-left: pw(40);
  margin-top: 10px;
}

.stripe {
  width: pw(100);
}

.CardLine {
  position: absolute;
  top: 0;
}

.CardHead {
  @include flex;
  margin-top: 40px;
  width: 145px;
  height: 145px;
  z-index: 1;
  background: var.$color-background;
  border: 2px solid #ffdc30;
  border-radius: 50%;
  margin-top: -25px;
}

.CardTitle {
  font-weight: 800;
  font-size: pw(36);
  text-transform: uppercase;
  color: #369feb;
}

.CardHeadImage {
  width: 96px;
  height: 96px;
  mix-blend-mode: screen;
}

.CardLine {
  width: 100px;
  height: 62px;

  img {
    width: 100%;
    height: 100%;
  }
}

.Text {
  font-weight: 400;
  font-size: pw(16);
  text-align: center;
  color: var.$color-text;
  opacity: 0.7;
  margin-top: pw(20);
}

.Button {
  margin-bottom: pw(25);
}

.CardDesc {
  font-weight: 400;
  text-align: left;
  font-weight: 400;
  font-size: 14px;
  line-height: 19px;
  color: #ffffff;
  opacity: 0.5;
}

@media (max-width: 768px) {
  .Volunteer {
    // padding: 0 15px pw(309);
    // min-height: 100vh;
  }

  .VolunteerLeft {
    width: 0vw;
    height: 100vh;
    background: #00193e;
  }

  .VolunteerRight {
    width: 100vw;
    height: 100vh;
    background: #002d71;
    padding: 100px 20px;
  }

  .stripe {
    width: 50px;
  }

  .CardHead {
    margin-top: -10px;
    width: 85px;
    height: 85px;
  }

  .CardTitle {
    font-size: 18px;
  }

  .CardHeadImage {
    width: 75px;
    height: 75px;
  }

  .CardDesc {
    width: 100%;
  }
}
