@use '../../Variables.scss' as var;
@import '../../assets/styles/Utils.scss';
@import '../../assets/styles/Adaptation.scss';

@mixin WarpSize {
  width: 50vw;
  height: 100vh;
  position: absolute;
  top: 0;
}

.BlueText {
  color: #369feb;
}

.Solution {
  color: #fff;
  z-index: 3;
  position: relative;
}

.IntelligentImage {
  width: pw(300);
  height: pw(300);
  margin-bottom: pw(76);
}

.OurVisionImage {
  margin-bottom: pw(76);
}

.HumansImage {
  position: fixed;
  right: 0;
  bottom: 0;
  width: pw(860);
}

.FoundUs {
  width: 88px;
  height: 136px;
  position: fixed;
  z-index: 10;
  right: 0;
  top: calc(50vh - 70px);
  cursor: pointer;
}

.TransitionWarp {
  @include flex();
  width: 100vw;
  height: 100vh;
  overflow: hidden;
}

.Left,
.Right {
  width: 50vw;
  height: 100vh;
  position: relative;
}

.Left {
  @include flex();
}

.Right {
  @include flex();
}

.LeftContent {
  @include flex();
  flex-direction: column;
}

.LeftContent,
.RightContent {
  position: relative;
}

.ContentWarp {
  @include flex();
  width: 100%;
  height: 100%;
  position: absolute;
  z-index: 7;
  mix-blend-mode: screen;
}

.LeftMask {
  @include WarpSize();
  left: 0;
  // background: #002d71;
  background: #000;
  // background: red;
  z-index: 10;
}

.RightMask {
  @include WarpSize();
  right: 0;
  background: #000;
  z-index: 10;
}

.NextBtn {
  text-align: center;
  margin-top: pw(80);
  cursor: pointer;
  font-size: 16px;
  transition: all ease 300ms;
  transform: translateY(0px);

  &:hover {
    transform: translateY(10px);
  }
}

.BackBtn {
  margin-bottom: pw(124);
  cursor: pointer;
  font-size: 16px;
  transition: all ease 300ms;
  transform: translateX(0px);

  &:hover {
    transform: translateX(-10px);
  }
}

.Title {
  font-weight: 800;
  font-size: 48px;
  color: #ffffff;
  text-align: center;
  line-height: 48px;
}

.TitleSec {
  font-weight: 800;
  font-size: 24px;
  margin-bottom: 40px;
}

.Content {
  width: pw(690);
  font-style: normal;
  font-weight: 400;
  font-size: 18px;
  line-height: 25px;
  color: #ffffff;
  opacity: 0.7;
  margin-bottom: pw(51);
}

.Progress {
  width: 48px;
  height: 8px;
  position: fixed;
  right: 40px;
  bottom: 60px;
  background: rgba($color: #fff, $alpha: 0.2);
}

.ProgressColor {
  background: #3f97db;
  height: 100%;
  width: 0%;
}

.SdkVideo {
  width: 60%;
  margin: 0 auto;
  display: block;
  margin-top: 30px;
  mix-blend-mode: screen;
}

@media (max-width: 768px) {
  .IntelligentImage {
    width: 150px;
    height: 150px;
    margin-bottom: pw(76);
  }

  .TransitionWarp {
    display: block;
    flex-wrap: wrap;
  }

  .SdkVideo {
    width: 80%;
    margin: 0 auto;
    margin-top: 30px;
  }

  .Left,
  .Right {
    width: 100vw;
    height: 50vh;
    position: relative;
  }

  .Right {
    padding: 0 30px;
  }

  .Title {
    font-size: 18px;
    line-height: 22px;
  }

  .TitleSec {
    font-size: 14px;
  }

  .Content {
    font-size: 12px;
    width: 100%;
    max-height: 150px;
    overflow: hidden;
  }

  .FoundUs img {
    position: fixed;
    height: 60px;
    z-index: 10;
    right: 0;
    top: calc(50vh - 30px);
    cursor: pointer;
  }

  .LeftMask,
  .RightMask {
    width: 100vw;
  }

  .HumansImage {
    width: 220px;
  }
}

.ContentWarp {
  padding: 20px;
}

.Hide {
  opacity: 0;
  cursor: none;
}
