@use '../../Variables.scss' as var;
@import '../../assets/styles/Utils';
@import '../../assets/styles/Adaptation.scss';

.Main {
  width: 100vw;
  height: 100vh;
  background: #000;
}

.Content {
  width: 100vw;
  height: 100vh;
  overflow: auto
}

.Header {
  @include flex(space-between);

  width: 100vw;
  position: fixed;
  z-index: 11;
  left: 0;
  top: 0;
  padding: pw(20) pw(40);
}

.HeaderLogoImage {
  width: pw(30);
  margin-right: pw(7);
}

.HeaderLogoText {
  width: pw(148);
}

.HeaderMenu {
  @include flex(flex-end);
  position: relative;
  z-index: 9;
}

.HeaderMenuItem {
  font-weight: 400;
  font-size: 16px;
  color: #ffffff;
  opacity: 0.7;
  margin-left: pw(40);
  cursor: pointer;
}

/** menu **/
.MenuLabel {
  margin-bottom: pw(130);
  font-size: pw(16);
  color: #ffffff;
  opacity: 0.7;
}

.MenuWarp {
  @include flex();
  position: fixed;
  z-index: 8;
  left: 0;
  top: 0;
  height: 100vh;
  padding: pw(60) pw(114);
  color: #fff;
}

.MenuItem {
  // margin: pw(24) 0;
  font-size: pw(24);
  margin-left: pw(35);
}

.ChildMenuItem {
  margin: pw(10) 0;
  font-size: pw(18);
  margin-left: pw(35);
}

.MenuItem .active {
  color: #369feb !important;
}

.MenuMask {
  position: fixed;
  z-index: 9;
  left: 0;
  top: 0;
  width: 100vw;
  height: 100vh;
}

@media (max-width: 768px) {
  .HeaderLogoImage {
    width: 35px;
    margin-right: 7px;
  }

  .HeaderLogoText {
    width: 108px;
  }

  .MenuLabel {
    font-size: 18px;
    margin-bottom: 60px;
  }

  .Header {
    padding: 10px 15px;
  }

  .MenuItem {
    font-size: 16px;
    margin-bottom: 10px;
  }

  .ChildMenuItem {
    font-size: 14px;
    margin-bottom: 10px;
  }
}

.Hamburger {
  width: 28px;
  cursor: pointer;
}

.HamburgerLine {
  display: block;
  width: 100%;
  height: 4px;
  background-color: #fff;
  margin-bottom: 6px;
  -webkit-transition: all 0.3s ease-in-out;
  transition: all 0.3s ease-in-out;
}

.HamburgerActive {
  .HamburgerLine:nth-child(1) {
    transform: translateY(10px) rotate(-45deg);
  }
  .HamburgerLine:nth-child(2) {
    opacity: 0;
  }
  .HamburgerLine:nth-child(3) {
    transform: translateY(-10px) rotate(45deg);
  }
}
